body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:link {
    text-decoration: none;
    color: white;
}

a:visited {
    text-decoration: none;
    color: white;
}

.parallax {
    height: 100%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

h1, h3 {
    margin: 0px;
}

/* start landscape */
@media (min-width: 769px) {

    h1, h3 {
        margin: 0px;
    }

    #npsLogo {
        width: 30px;
        text-align: left;
    }

    .App {
        display: grid;
        grid-template-rows: auto repeat(6, 1fr);
        grid-template-columns: 50px 1fr 50px;
        text-align: center;
        height: 100vh;
        color: white;
    }

    .header {
        /* in App */
        grid-row: 1/2;
        grid-column: 1/-1;
        padding: 20px;
        padding-bottom: 0px;
        /* in header */
        display: grid;
        grid-template-rows: repeat(3, auto);
        grid-template-columns: repeat(2, 1fr);
        color: black;
    }

    #takeHike {
        grid-row: 1/2;
        grid-column: 1/-1;
    }

    #instruct {
        grid-row: 2/3;
        grid-column: 1/-1;
        padding: 5px;
    }

    .poweredBy {
        grid-row: 3/4;
        grid-column: 1/2;
        text-align: right;
        padding-right: 10px;
        padding-top: 10px;
    }

    .logoDiv {
        grid-row: 3/4;
        grid-column: 2/3;
        text-align: left;
    }

    .mapDiv {
        /* in App */
        grid-row: 2/3;
        grid-column: 2/3;
        width: 600px;
        margin: auto;
    }

    .parkResultsDiv {
        /* in App */
        grid-row: 3/4;
        grid-column: 2/3;
        /* in parkResultsDiv */
        display: grid;
        grid-template-rows: 25px repeat(5, auto);
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 10px;
    }

    .selectedState {
        /* in parkResultsDiv */
        grid-row: 1/2;
        grid-column: 1/-1;
        text-align: center;
    }

    .spinner {
        /* in parkResultsDiv */
        width: 75px;
        border-radius: 50%;
    }

}
/* end landscape */
@media (max-width: 1024px) {

    .parkResult {
        padding: 10px;
        box-shadow: 0 0 10px black;
    }

    .parkImages {
        width: 125px;
    }

    .clamp-lines__button {
        /* border: hidden; */
        margin: 2px;
        background: none;
        border: 0;
        color: red;
        cursor: pointer;
        padding: 0;
        font-size: 10pt;
    }

    #clamped-content-readMore {
        margin: 2px;
    }

}

@media (min-width: 769px) {

    .parkResult {
        padding: 10px;
        box-shadow: 0 0 10px black;
    }
    
    .parkImages {
        width: 200px;
    }

    .clamp-lines__button {
        /* border: hidden; */
        margin: 2px;
        background: none;
        border: 0;
        color: red;
        cursor: pointer;
        padding: 0;
        font-size: 10pt;
    }

    #clamped-content-readMore {
        margin: 2px;
    }

}
.state {
    fill: white;
    opacity: 0.70;
}

path:hover {
    fill: black;
    opacity: 0.75;
}

